.node {
  outline: 0.001vh solid rgb(175, 216, 248);
  display: inline-block;
  vertical-align: top;
  background-color: whitesmoke;
}

.node-start {
  background-color: rgba(8, 211, 8, 0.8);
  /* Image downloaded from  https://fontawesome.com/*/
  background-image: url("./Icons/play-solid.svg");
  background-repeat: no-repeat;
  background-size: 55%;
  background-position: center;
}

.node-finish {
  background-color: rgb(255, 0, 0, 0.8);
  /* Image downloaded from  https://fontawesome.com/*/
  background-image: url("./Icons/trophy-solid.svg");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center;
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.2s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.start-node-visited {
  background-image: url("./Icons/play-solid.svg");
  background-repeat: no-repeat;
  background-size: 55%;
  background-position: center;
  animation-name: visitedAnimation;
  animation-duration: 1.2s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.finish-node-visited {
  background-image: url("./Icons/trophy-solid.svg");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center;
  animation-name: visitedAnimation;
  animation-duration: 1.2s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.weight-node-visited {
  background-image: url("./Icons/dumbbell-solid.svg");
  background-repeat: no-repeat;
  background-size: 68%;
  background-position: center;
  animation-name: visitedAnimation;
  animation-duration: 1.2s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(17, 104, 217, 0.9);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.9);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.9);
  }

  100% {
    transform: scale(1);
    background-color: rgba(0, 190, 218, 0.9);
  }
}

.node-wall {
  background-color: rgb(12, 53, 71);
}

.node-weight {
  background-image: url("./Icons/dumbbell-solid.svg");
  background-repeat: no-repeat;
  background-size: 68%;
  background-position: center;
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.start-node-shortest-path {
  background-image: url("./Icons/play-solid.svg");
  background-repeat: no-repeat;
  background-size: 55%;
  background-position: center;
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.finish-node-shortest-path {
  background-image: url("./Icons/trophy-solid.svg");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center;
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.weight-node-shortest-path {
  background-image: url("./Icons/dumbbell-solid.svg");
  background-repeat: no-repeat;
  background-size: 68%;
  background-position: center;
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}
